.row-block {
  display: flex;
  justify-content: flex-start;
  div {
    padding: 4px;
  }
}
.row-block_header {
  font-weight: 600;
  background-color: rgb(145, 157, 169);
  padding: 4px;
  // border-right: 1px solid grey;
  // max-width: 120px;
}
.row-block_body {
  padding: 4px;
  // border-right: 1px solid grey;
  text-align: center;
  background-color: #e2e0a8;
  input {
    border: 1px solid transparent;
    background-color: #f5f1f16b;
    &:hover {
      background-color: #fff;
    }
    &:active {
      background-color: #fff;
    }
    &:focus {
      background-color: #fff;
    }
  }
  select {
    min-width: 40px;
  }

  tr.odds {
    background-color: rgb(223, 223, 223);
    height: 20px;
    // border-bottom: 1px solid grey;
  }
  
  tr.even {
    background-color: rgb(233, 233, 233);
    height: 20px;
    // border-bottom: 1px solid grey;
  }

}
