

#header {
  display: flex;
  max-height: 40px;
  background-color: #e2e0a8;
  .userOrg {
    margin: 4px 8px;
    p {
      padding: 0px;
      margin: 0px 8px;
    }
  }
  img {
    border-style: none;
    border-width: 0;
    width: 239px;
    aspect-ratio: auto 239 / 30;
    height: 30px;
    margin: 4px;
  }
  .header-menu {
    display: flex;
    a {
      padding: 10px 6px;
      color: #000000;
      text-decoration: none;
      cursor: pointer;
    }
  }
}

.title {
  font-size: 20px;
  text-align: center;
  margin: 0 auto;
  padding: 12px;

}