$app-background-color: rgb(170, 26, 26);
$app-font-size: 0.8rem;
$app-padding: 4px;
$color-p: #162e87;
 

img {
  // width: 100%;
  border-style: none;
  border-width: 0;
}


.bold {
  color: #000000;
  font-family: Verdana, Geneva, Arial, Helvetica, sans-serif;
  font-weight: bold;
}

 
#remember {
  margin-inline: 4px;

}

.info-block {
  font-size: $app-font-size;
  li {
    text-align: left;
    padding-left: 8px;
    color: $color-p;
  }
  p{
    font-size: $app-font-size;
    color: $color-p;
  }
}


.contact-block_header {
  text-align: center;
  font-weight: bold;
  color: $color-p;
  padding: 4px 0;
}

.contact-block {

  .contact-block_content {
    display: flex;
    flex-direction: column;
    // font-size: $app-font-size;
    margin-top: 4px;
    margin-bottom: 4px;
    .contact-block_content_title {
      text-align: left;
      padding: $app-padding;
      // font-size: $app-font-size;
    }
    .contact-block_content_body {
      display: flex;
      justify-content: left;
      gap: 12px;
    }
    
  }
  

}




.contact-block_content_phone {
  display: flex;
  gap: 4px;
}

.contact-block_content_phone i {
  font-size: small;
}
.contact-block_content_phone a {
  font-size: small;
}

.contact-block_content_email {
  display: flex;
  gap: 4px;
}

.img-certificat {
  margin: 16px auto;
}

.copyright-block {
  display: flex;
  justify-content: center;
  // font-size: $app-font-size;
  padding: 10px;
  align-items: center;
  .copyright-a {
    white-space: nowrap;
  }
}

.img-pic {
  width: 198px;
  height: 98px;
}

.baner { display: flex; justify-content: center }
.info-panel-footer { 
  display: flex; 
  justify-content: center;
  border: 1px solid grey;
  padding: 10px;
  border-radius: 26px;
  img {
    height: 128px;
    width: 46px;
  }
  p{
    font-size: $app-font-size;
    color: $color-p;
  }

}
