.tab-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start; 
  border: 1px solid rgba(48, 46, 46, 0.153);
  // border: 1px solid rgba(128, 128, 128, 0.153);
  margin-top: 0px; 
  .tcnt {
    margin-top: 15px;
    display: table;
    border-collapse: collapse;
    box-sizing: border-box;
    text-indent: initial;
    border-spacing: 0px;
    border-color: grey;
    word-wrap: break-word; /* Перенос слов */ 
  }
  
}

.tab-summ {
  margin-top: 4px; 
  border: 1px solid rgba(48, 46, 46, 0.153);
  align-self: flex-end;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 10px 5px;
  .line-block_value {
    min-width: 70px;
    text-align: left;
  }
}